@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
.App{font-family:sans-serif;text-align:center;font-family:Arial, Helvetica, sans-serif}*{box-sizing:border-box}body{background-color:#f3f3f3;padding:20px 10px;margin:0;overflow-x:hidden}.num{line-height:100%;font-family:'Open Sans', sans-serif;font-weight:600;width:3.8cm;height:3.8cm;border-radius:50%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:45.8pt;border:0.04cm dashed black;box-sizing:content-box;cursor:pointer;transition:all 0.12s}.num:hover{background-color:#ff6969}.price{overflow:hidden;margin:0 auto;padding:1.1cm;width:21cm;height:29.7cm;display:-webkit-flex;display:flex;-webkit-align-items:flex-start;align-items:flex-start;-webkit-align-content:flex-start;align-content:flex-start;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-column-gap:0.78cm;-webkit-column-gap:0.78cm;column-gap:0.78cm;grid-row-gap:0.78cm;row-gap:0.78cm;background-color:#fff}.inp{margin:0 auto;margin-bottom:30px;height:45px;display:-webkit-flex;display:flex;width:200px;-webkit-justify-content:center;justify-content:center;-webkit-align-self:center;align-self:center}.inp input{font-size:18px;height:100%;-webkit-flex:0 0 85%;flex:0 0 85%;padding:8px 15px;text-align:center;border-radius:50px;border:1px solid greenyellow;outline:none}.inp input:focus{outline:none}.inp button{font-size:18px;font-weight:600;display:inline-block;-webkit-flex:0 0 45px;flex:0 0 45px;height:45px;border-radius:15px;outline:none;border:1px solid greenyellow;margin-left:10px;background-color:#fff;transition:all 0.12s}.inp button:active{background-color:greenyellow}.btn{font-size:18px;font-weight:600;display:inline-block;-webkit-flex:0 0 45px;flex:0 0 45px;height:45px;border-radius:15px;outline:none;border:1px solid greenyellow;margin-left:10px;background-color:#fff;transition:all 0.12s;cursor:pointer}.btn:hover{background-color:rgba(172,255,46,0.2)}.btn:active{background-color:greenyellow}.btn_print{width:auto;padding:7px 15px;margin-bottom:20px}@page{size:auto;margin:0mm}@media screen and (max-width: 900px){.price{width:100%;height:auto;min-height:29.7cm}}@media print{.head{display:none}.price{width:21cm;height:29cm}}

